import React from "react"
import FriendsContainer from "./Friends/FriendsContainer"
import NavBarLink from "./NavBarLink/NavBarLink"
import styles from './navbat.module.css'
const Nav = (props) => {
  return (

    <div className={styles.body_nav}>
      <ul className={styles.nav__list}>
        <NavBarLink from='/Profile' title=' ' />
        <NavBarLink from='/News' title='' />
        <NavBarLink from='/Massage' title='' />
        <NavBarLink from='/Photo' title='' />
        <NavBarLink from='/Users' title='' />
        <NavBarLink from='/Groups' title='' />
        <NavBarLink from='/Frends' title='' />

      </ul >
      <FriendsContainer  />
    </div >
  )
}

export default Nav